			})
		},
		touchstart(e){
			this.startClientX = e.changedTouches[0].clientX;
		},
		touchend(e){
			if(this.tabCur == 0 && e.changedTouches[0].clientX - this.startClientX < -100){
				
				this.tabCur = 1
				
				uni.pageScrollTo({
					duration: 0,
					scrollTop: 0
				})
			} else if(this.tabCur == 1 && e.changedTouches[0].clientX - this.startClientX > 100){
				this.tabCur = 0
				
				uni.pageScrollTo({
					duration: 0,
					scrollTop: 0
				})
			}
			
		},
	}
